<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <el-button   @click="createNew">创建新客户</el-button>
      </div>
    </div>
    <div class="list-center">
      <!--列表-->
      <el-table :data="tableData"  v-loading="listLoading" @selection-change="" class="chanceTable">
        <el-table-column type="index" label="编号" width="60" height="40" align="center">
        </el-table-column>
        <el-table-column prop="name" label="名称" align="center" >
        </el-table-column>
        <el-table-column prop="sex" label="性别" align="center"  >
        </el-table-column>
        <el-table-column prop="contactPhone" label="联系人手机号"  align="center"  >
        </el-table-column>
        <el-table-column label="操作" width="180" align="center">
          <template scope="scope">
            <el-button size="small" @click="lookDetail(scope.$index, scope.row)">查看</el-button>
            <el-button type="danger" size="small" @click="">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="prev, pager, next,total" @current-change="" :page-size="10" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'list',
    components: {},
    data () {
      return {
        total: 7,
        page: 10,
        listLoading: false,
        tableData: [{
          name: '张三',
          sex: '男',
          contactPhone: '138-0013-8000'
        }, {
          name: '李四',
          sex: '女',
          contactPhone: '138-0013-8000'
        }, {
          name: '王二麻子',
          sex: '男',
          contactPhone: '138-0013-8000'
        }, {
          name: '张三',
          sex: '男',
          contactPhone: '138-0013-8000'
        }, {
          name: '李四',
          sex: '男',
          contactPhone: '138-0013-8000'
        }, {
          name: '王二麻子',
          sex: '男',
          contactPhone: '138-0013-8000'
        }, {
          name: '张三',
          sex: '男',
          contactPhone: '138-0013-8000'
        }]
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      createNew () {
        this.$router.push({
          path: '/crm/customerDetail'
        })
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
        this.editModal = true
        this.currIndex = index
        if (index === -1) { // 新增
          this.currDate = {
            createdAt: '',
            desc: '',
            publishedAt: '',
            type: '',
            used: true,
            who: '',
            url: 'c.fwone.com'
          }
        } else { // 编辑
          this.currDate = this.listData[index]
        }
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      },
      /**
       * 查看
       */
      lookDetail () {
        this.$router.push({
          path: '/crm/customerDetail'
        })
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
    }
  }
</script>





